/* ============================================================
   SISTEMA DE TEMAS — Starter Kit (2026)
============================================================ */

:root {
  --bg: #ffffff;
  --text-primary: #000000;
  --text-heading: #000000;

  --accent: #0000ff;
  --accent-hover: #0000cc;
  --accent-rgb: 0, 0, 255;

  --bg-img: none;

  /* Texto em cima do accent */
  --text-on-accent: #ffffff;

  /* Links globais (header, burger, dropdown) */
  --link: #333;
  --link-hover: #000;
}

/* LIGHT THEME */
html.light-theme {
   background-blend-mode: soft-light;
   opacity: 0.98;
  --bg: #ffffff;
  --text-primary: #1a1a1a;
  --text-heading: #000000;

  --accent: #0055ff;
  --accent-hover: #0044cc;
  --accent-rgb: 0, 85, 255;

  --text-on-accent: #ffffff;

  --link: #1a1a1a;
  --link-hover: #0055ff;

  --bg-img: url('../img/themes/light-bg.jpg');
}

/* DARK THEME */
html.dark-theme {
  --bg: #0d0d0d;
  --text-primary: #e0e0e0;
  --text-heading: #ffffff;

  --accent: #00ffea;
  --accent-hover: #00d1c1;
  --accent-rgb: 0, 255, 234;

  --text-on-accent: #000000;

  --link: #e0e0e0;
  --link-hover: #00ffea;

  --bg-img: url('../img/themes/dark-bg.jpg');
}

/* HYDRA THEME */
html.hydra-theme {
  --bg: #050505;
  --text-primary: #00ff41;
  --text-heading: #d1ffd1;

  --accent: #00ff41;
  --accent-hover: #00cc33;
  --accent-rgb: 0, 255, 65;

  --text-on-accent: #000000;

  --link: #00ff41;
  --link-hover: #00cc33;

  --bg-img: url('../img/themes/hydra-bg.jpg');
}

html.solarized-theme {
  --bg: #fdf6e3;
  --text-primary: #657b83;
  --text-heading: #073642; /* Azul escuro original do Solarized */
  --accent: #b58900;
  --text-on-accent: #ffffff;
  --accent-rgb: 181, 137, 0;
  --bg-img: linear-gradient(135deg, #fdf6e3 0%, #eee8d5 100%);
  --bg-img: url('../img/themes/solarized-bg.jpg');
}


/* DRACULA THEME */
html.dracula-theme {
  --bg: #282a36;
  --text-primary: #f8f8f2;
  --text-heading: #ffffff;

  --accent: #bd93f9;
  --accent-hover: #a371f7;
  --accent-rgb: 189, 147, 249;

  --text-on-accent: #000000;

  --link: #f8f8f2;
  --link-hover: #bd93f9;

  --bg-img: url('../img/themes/dracula-bg.jpg');
}



/* NEON THEME — Contraste Máximo */
html.neon-theme {
  --bg: #0aff9d;
  --text-primary: #000000;
  --text-heading: #000000; /* Títulos pretos para "furar" o verde */
  --accent: #000000;
  --text-on-accent: #0aff9d;
  --accent-rgb: 0, 0, 0;    /* Blob preto cria sombras pulsantes no verde */
  
  --link: #000000;
  --link-hover: #ffffff;
  --bg-img: linear-gradient(135deg, #fdf6e3 0%, #eee8d5 100%);
  --bg-img: url('../img/themes/neon-bg.jpg');
}


/* ============================================================
   BACKGROUND GLOBAL + FOOTER
============================================================ */

html {
  background-color: var(--bg);
  background-image: var(--bg-img);
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  transition: background 0.5s ease-in-out;
}


footer {
  background: rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(10px);
}
