.hero {
  padding-top: 7rem;

  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  min-height: 64.25vh; /* altura mínima para ocupar a tela inteira, considerando header e footer */
  padding-left: 2rem;
  padding-right: 2rem;
  padding-bottom: 1.5rem;
  /* IMPORTANTE: nada de gap aqui */
}

.hero .container { 
  display: flex;
  flex-direction: column;
  gap: 2rem; /* espaçamento correto entre título, texto e botão */
}

.hero .container .hero_btn {
  align-self: center; /* alinhamento do botão */
}

.hero_description {
  font-size: 1.2rem;
  color: var(--accent);
}

.hero_description--last {
  font-weight: bold;
  color: var(--link-hover);
}
#home {
  position: relative; 
  z-index: 1;
  min-height: 50vh;
  padding-top: 6rem;

  display: none;
  opacity: 1;
  transform: translateY(40px);
  transition: opacity 0.6s ease, transform 0.6s ease;
}



.home-section .main {
  display: grid;
  grid-template-columns: 1fr 1fr; /* duas colunas iguais */
  gap: 2rem; /* espaço entre as colunas */
}
h1, .home-heading {
  color: var(--text-heading);
  font-weight: 900 !important; /* Máxima espessura */
  letter-spacing: -1.5px;      /* Letras mais densas e unidas */
  
  /* No Neon, adicionamos este "punch" */
  html.neon-theme & {
    text-shadow: 1px 1px 0px rgba(255, 255, 255, 0.2); // Um leve brilho por trás do preto
    filter: drop-shadow(0 0 1px rgba(0,0,0,0.1));
  }
}

.home-heading {
  font-size: 1.6rem;
  margin-bottom: 2rem;
  color: var(--text-heading);
  text-align: center;
}

.home-section p {
  text-align: justify;
  text-justify: inter-word;
  hyphens: auto;
}



#home.visible {
  display: block;
  opacity: 1;
  transform: translateY(0);
}


